import Vue from 'vue'
import VueRouter from 'vue-router'//使用路由时，要引入vue-router
import foo from "@/components/foo";
import home from "@/components/home";
import notfound from '@/components/404'
import ajax from "@/components/ajax";
import pridect from "@/components/pridect";
import recommend from "@/components/recommend";
import search_list from "@/components/search_list";

Vue.use(VueRouter)

export default new VueRouter({
    routes:[
        {
            path:'/',
            component: home,
            meta:{
                title:'首页'
            }
        },
        {
            path:'/pridect',
            component:pridect,
            meta:{
                title:'票房预测'
            }
        },
        {
            path:'/recommend',
            component:recommend,
            meta:{
                title:'演员推荐'
            }
        },
        {
            path:'/search_list',
            component:search_list,
            meta:{
                title:'搜索结果'
            }
        },
        {
            path:'/foo',
            component:foo,
            meta: {
                title: 'sub'
            }
        },{
            path:'/ajax',
            component:ajax,
            meta:{
                title:'ajax数据'
            }
        },{
            path:'*',
            component:notfound,
            meta:{
                title:'404'
            }
        }]
})

