<template>
    <div>
        <img src="~assets/logo.png">
        <p>{{info}}</p>
    </div>
</template>

<script>
    export default {
        name: "ajax",
        data(){
            return{
                info:''
            }
        },
        mounted(){
            this.axios.get('http://date.jsontest.com/').then(res=>{
                this.info=res.data.date
            })
        }
    }
</script>

