<template>
    <div class="search">
        <div class="logo">
            <img />
        </div>
        <div class="search_input">
            <input type="text" v-model="words" placeholder="请输入要搜索的关键词" @keyup.enter="SearchItem"/>
            <button @click="SearchItem" >搜索</button>
            <div class="radios">
                <el-radio v-model="type" label="1">电影</el-radio>
                <el-radio v-model="type" label="2">导演</el-radio>
                <el-radio v-model="type" label="3">演员</el-radio>
                <el-radio v-model="type" label="4">制片人</el-radio>
                <el-radio v-model="type" label="5">编剧</el-radio>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: "search",
        data(){
            return {
                words:'',
                type:'1'
            }
        },
        methods:{
            SearchItem:function () {
                if(this.words=='') alert('当前未输入')
                else{
                    this.$router.push({
                        path:'/search_list',
                        query:{
                            words:this.words,
                            type:this.type
                        }
                    })
                }
            }
        }
    }
</script>

<style scoped>
    .search{
        width: 1200px;
        height: 500px;
        margin: 0 auto;
    }
    .logo,.search_input{
        width: 750px;
        height: 200px;
        text-align: center;
        margin: 0 auto;
    }
    .search_input input{
        width: 600px;
        height: 30px;
        font-size: 18px;
        float: left;
        padding: 10px 16px;
        border: 1px solid gray;
        border-right: 0;
    }
    .search_input button{
        float: left;
        height: 52px;
        width: 100px;
        border: 0;
        background-color: royalblue;
        color: white;
        font-size: 18px;
        cursor: pointer;
    }
    .search_input button:hover{
        background-color: blue;
    }
    .radios{
        float: left;
        margin-top: 10px;
    }
</style>