<template>
    <div>
        <div class="top">
            <p>包含{{this.words}}的{{this.type}}搜索结果如下</p>
        </div>
        <div class="box">
            <div v-if="this.error">
                <span>{{this.error_string}}</span>
            </div>
            <div v-else>
                <ul>
                    <li v-for="movie in this.datas" :key="movie">
                        <div>
                            <div class="title">{{movie.name}}</div>
                            <div class="info">{{movie.info}}</div>
                        </div>
                    </li>
                </ul>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: "search_list",
        data(){
            return{
                words:this.$route.query.words,
                type:this.$route.query.type,
                datas:[],
                error:true,
                error_string:"正在搜索"
            }
        },
        mounted(){
            document.title=this.words+'的搜索结果如下';
            var url='http://'+window.location.host+"/search";
            this.axios.get(url,{
                params:{
                    words:this.words,
                    type:this.type
                }
            }).then(res=>{
                if(res.data.msg==0){
                    this.error_string="没有此功能";
                }
                else if(res.data.msg==1){
                    this.datas=res.data.datas;
                    this.error=false;
                }

            })
        }
    }
</script>

<style scoped>
    .top{
        width: 1200px;
        height: auto;
        margin: 0 auto;
        background-color: royalblue;
        color: white;
    }
    .top p{
        font-size: 20px;
        padding: 10px;
    }
    .box{
        width: 1200px;
        height: auto;
        margin: 0 auto;
    }
    .box ul{
        list-style: none;
        padding: 0;
        margin: 0;
    }
    .box ul li{
        border-bottom: 1px solid gray;
        padding: 10px 0;
    }
    .title{
        color: darkgrey;
    }
    .info{
        color: black;
        font-size: 14px;
    }
</style>